"use client"

import { useState } from "react"
import { Box, ThemeProvider, CssBaseline, createTheme } from "@mui/material"
import { useAuth } from "../context/AuthContext"

// Import components
import ScrollHeader from "../components/Header/ScrollHeader.jsx"
import HeroSection from "../components/Hero/HeroSection.jsx"
import BelowHeroSection from "../components/Sections/BelowHeroSection.jsx"
import WhyChooseUsSection from "../components/Sections/WhyChooseUsSection.jsx"
import FeaturesSection from "../components/Sections/FeaturesSection.jsx"
import FeedbackSection from "../components/Sections/FeedbackSection.jsx"
import FAQSection from "../components/Sections/FAQSection.jsx"
import ContactSection from "../components/Sections/ContactSection.jsx"
import AuthModal from "../components/Auth/AuthModal.jsx"
import { Dashboard } from "../components/dashboard"

const theme = createTheme({
  palette: {
    primary: { main: "#4CAF50" },
    secondary: { main: "#FFC107" },
  },
  typography: {
    fontFamily: "'Inter', -apple-system, BlinkMacSystemFont, sans-serif",
  },
})

const LandingPageContent = () => {
  const [authModalOpen, setAuthModalOpen] = useState(false)
  const [authMode, setAuthMode] = useState("login")
  const { user, loading } = useAuth()

  const handleLoginClick = () => {
    setAuthMode("login")
    setAuthModalOpen(true)
  }

  const handleSignUpClick = () => {
    setAuthMode("signup")
    setAuthModalOpen(true)
  }

  const handleCloseAuthModal = () => {
    setAuthModalOpen(false)
  }

  if (loading) {
    return (
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          minHeight: "100vh",
          backgroundColor: "#f5f5f5",
          position: "fixed",
          top: 0,
          left: 0,
          width: "100%",
          zIndex: 9999,
        }}
      >
        <Box sx={{ textAlign: "center" }}>
          <Box
            sx={{
              width: 60,
              height: 60,
              borderRadius: "50%",
              background: "linear-gradient(135deg, #4CAF50, #45a049)",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              margin: "0 auto 1rem auto",
              animation: "pulse 2s infinite",
              "@keyframes pulse": {
                "0%": { transform: "scale(1)" },
                "50%": { transform: "scale(1.1)" },
                "100%": { transform: "scale(1)" },
              },
            }}
          >
            <Box
              sx={{
                color: "white",
                fontWeight: "bold",
                fontSize: "1.5rem",
              }}
            >
              DT
            </Box>
          </Box>
        </Box>
      </Box>
    )
  }

  if (user) {
    return <Dashboard />
  }

  return (
    <>
      {/* Header - Fixed at top */}
      <ScrollHeader onLoginClick={handleLoginClick} onSignUpClick={handleSignUpClick} />

      {/* Main Content Container */}
      <Box
        sx={{
          position: "relative",
          width: "100%",
          minHeight: "100vh",
          backgroundColor: "#ffffff",
          // Ensure proper stacking
          zIndex: 1,
        }}
      >
        {/* Hero Section */}
        <HeroSection />

        {/* Below Hero Section */}
        <BelowHeroSection />

        {/* Rest of the sections */}
        <WhyChooseUsSection />
        <FeaturesSection />
        <FeedbackSection />
        <FAQSection />
        <ContactSection />
      </Box>

      {/* Auth Modal */}
      <AuthModal open={authModalOpen} onClose={handleCloseAuthModal} mode={authMode} />
    </>
  )
}

// Main LandingPage component that provides the theme
const LandingPage = () => {
  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Box
        sx={{
          position: "relative",
          width: "100%",
          minHeight: "100vh",
          backgroundColor: "#ffffff",
          // Reset any global styles
          "& *": {
            boxSizing: "border-box",
          },
        }}
      >
        <LandingPageContent />
      </Box>
    </ThemeProvider>
  )
}

export default LandingPage
