"use client"

import { useState, useEffect } from "react"
import { Box } from "@mui/material"
import { motion } from "framer-motion"

const BackgroundMathFormulas = ({ showFormulas }) => {
  const [isMobile, setIsMobile] = useState(false)

  useEffect(() => {
    const checkScreenSize = () => {
      setIsMobile(window.innerWidth < 768)
    }

    checkScreenSize()
    window.addEventListener("resize", checkScreenSize)

    return () => window.removeEventListener("resize", checkScreenSize)
  }, [])

  const desktopMathFormulas = [
    { formula: "∮ E⃗ · dA⃗ = Q/ε₀", left: "1%", top: "15%", fontSize: 20, delay: 0.1, color: "#43e97b" },
    { formula: "∇²φ = 4πGρ", left: "85%", top: "20%", fontSize: 18, delay: 0.3, color: "#43e97b" },
    { formula: "E = mc²", left: "2%", top: "75%", fontSize: 22, delay: 0.5, color: "#43e97b" },
    { formula: "∂u/∂t = k∇²u", left: "88%", top: "65%", fontSize: 16, delay: 0.7, color: "#43e97b" },
    { formula: "∫₋∞^∞ e^(-x²) dx = √π", left: "20%", top: "45%", fontSize: 19, delay: 0.9, color: "#43e97b" },
    { formula: "det(A - λI) = 0", left: "78%", top: "40%", fontSize: 17, delay: 1.1, color: "#43e97b" },
    { formula: "lim[n→∞] (1 + 1/n)ⁿ = e", left: "5%", top: "60%", fontSize: 15, delay: 1.3, color: "#43e97b" },
    { formula: "∑ₖ₌₁^∞ 1/k² = π²/6", left: "92%", top: "85%", fontSize: 18, delay: 1.5, color: "#43e97b" },
    { formula: "∇ × B⃗ = μ₀J⃗", left: "65%", top: "25%", fontSize: 16, delay: 1.7, color: "#43e97b" },
    { formula: "ψ(x,t) = Ae^(i(kx-ωt))", left: "70%", top: "78%", fontSize: 14, delay: 1.9, color: "#43e97b" },
    { formula: "∂²u/∂x² = (1/c²)∂²u/∂t²", left: "5%", top: "30%", fontSize: 15, delay: 2.1, color: "#43e97b" },
    { formula: "∇·D⃗ = ρ", left: "75%", top: "55%", fontSize: 17, delay: 2.3, color: "#43e97b" },
  ]

  const mobileMathFormulas = [
    { formula: "E = mc²", left: "15%", top: "20%", fontSize: 18, delay: 0.2, color: "#f093fb" },
    { formula: "∇²φ = 4πGρ", left: "75%", top: "25%", fontSize: 16, delay: 0.6, color: "#764ba2" },
    { formula: "∫ f(x)dx", left: "20%", top: "65%", fontSize: 20, delay: 1.0, color: "#43e97b" },
    { formula: "∑ₖ₌₁^∞ 1/k²", left: "80%", top: "70%", fontSize: 17, delay: 1.4, color: "#a8edea" },
    { formula: "∇ × B⃗ = μ₀J⃗", left: "10%", top: "45%", fontSize: 15, delay: 1.8, color: "#d299c2" },
    { formula: "lim[n→∞] (1 + 1/n)ⁿ", left: "85%", top: "50%", fontSize: 14, delay: 2.2, color: "#fee140" },
  ]

  const formulas = isMobile ? mobileMathFormulas : desktopMathFormulas

  return (
    <>
      <Box
        sx={{
          position: "absolute",
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          overflow: "hidden",
          zIndex: 999,
          pointerEvents: "none",
        }}
      >
        {formulas.map((item, index) => (
          <Box
            key={`${isMobile ? "mobile" : "desktop"}-${index}`}
            component={motion.div}
            initial={{
              opacity: 0,
              scale: 0.3,
              y: 100,
            }}
            animate={{
              opacity: showFormulas ? 0.5 : 0,
              scale: showFormulas ? 1 : 0.3,
              y: showFormulas ? 0 : 100,
            }}
            transition={{
              delay: showFormulas ? item.delay : 0,
              duration: 1.2,
              ease: "easeOut",
              type: "spring",
              stiffness: 100,
              damping: 15,
            }}
            whileHover={{
              scale: 1.4,
              opacity: 0.9,
              y: -10,
              transition: {
                duration: 0.3,
                ease: "easeOut",
              },
            }}
            sx={{
              position: "absolute",
              left: item.left,
              top: item.top,
              fontSize: item.fontSize,
              fontFamily: "'JetBrains Mono', 'Fira Code', monospace",
              fontWeight: "500",
              cursor: "pointer",
              userSelect: "none",
              color: item.color,
              textShadow: `0 0 15px ${item.color}60`,
              transition: "all 0.4s cubic-bezier(0.4, 0, 0.2, 1)",
              transformOrigin: "center",
              pointerEvents: showFormulas ? "auto" : "none",
              "&:hover": {
                textShadow: `0 0 25px ${item.color}90, 0 0 35px ${item.color}60`,
                filter: `drop-shadow(0 0 20px ${item.color}70)`,
              },
              "&:active": {
                scale: 0.95,
                transition: "scale 0.1s ease",
              },
            }}
          >
            <Box
              component={motion.div}
              animate={{
                y: [0, -5, 0],
                scale: [1, 1.2, 1],
              }}
              transition={{
                duration: 4 + index * 0.8,
                repeat: Number.POSITIVE_INFINITY,
                ease: "easeInOut",
                delay: index * 0.5,
              }}
            >
              {item.formula}
            </Box>
          </Box>
        ))}

        {showFormulas && (
          <>
            {[...Array(isMobile ? 8 : 15)].map((_, i) => (
              <Box
                key={`particle-${i}`}
                component={motion.div}
                initial={{
                  opacity: 0,
                  scale: 0,
                }}
                animate={{
                  opacity: [0, 0.3, 0],
                  scale: [0, 1, 0],
                  x: [0, Math.random() * 100 - 50],
                  y: [0, Math.random() * 100 - 50],
                }}
                transition={{
                  duration: 3 + Math.random() * 2,
                  repeat: Number.POSITIVE_INFINITY,
                  delay: Math.random() * 2,
                  ease: "easeInOut",
                }}
                sx={{
                  position: "absolute",
                  left: `${Math.random() * 100}%`,
                  top: `${Math.random() * 100}%`,
                  width: "2px",
                  height: "2px",
                  borderRadius: "50%",
                  background: `linear-gradient(45deg, #667eea, #764ba2)`,
                  boxShadow: "0 0 10px #667eea50",
                  pointerEvents: "none",
                }}
              />
            ))}
          </>
        )}
      </Box>
    </>
  )
}

export default BackgroundMathFormulas
